                    <path d="M17.323,23.657a1.333,1.333,0,0,0,.946-2.279l-7.063-7.049L18.269,7.28a1.338,1.338,0,1,0-1.892-1.892l-8,8a1.333,1.333,0,0,0,0,1.879l8,8a1.333,1.333,0,0,0,.946.4Z" transform="translate(-7.99 -4.996)" fill="currentColor" />
