                    {/* <div className="rights_modal_img_wrapper w-auto bg-light">
                            <img className="object-center-contain" src="https://images.unsplash.com/photo-1659535832621-4ef57c0e141b?crop=entropy&cs=tinysrgb&fit=max&fm=jpg&ixid=MnwyMjUyOTR8MXwxfGFsbHwxfHx8fHx8Mnx8MTY2ODUwNTA2NA&ixlib=rb-4.0.3&q=80&w=1200" height={200} width={1200} alt="ugc" />
                        </div> */}


